import React, { useState, Fragment, useCallback, useEffect, useRef } from 'react';
import { Canvas, extend } from 'react-three-fiber/web.cjs';
// import { Canvas, extend } from 'react-three-fiber';
import { AxesHelper } from 'three/src/helpers/AxesHelper';
import { isMobile } from 'react-device-detect';
import { PcControls, MobileControls, tryDeviceEvent } from 'Controls';
import { AllScenes } from 'Scenes';
import { Buttons, ChangeButton, MiniMap } from 'Interface';
import { Preload } from 'react-preload';

extend({ AxesHelper });

function getLargest(arr) {
  const newArr = [];
  arr.forEach((val) => {
    Array.prototype.push.apply(newArr, val.position);
  });
  const _newArr = newArr.map((val) => {
    return Math.abs(val);
  });
  return Math.max.apply(null, _newArr);
}

function App() {
  const [isPcControls, switchControls] = useState(true);
  const [positionArr, setPositionArr] = useState([]);
  const miniMapRef = useRef();
  const buttonsRef = useRef();
  const allSceneRef = useRef();
  const pcControlsRef = useRef();
  const controlProps = {
    enablePan: false,
    enableDamping: true,
    dampingFactor: 0.2,
    rotateSpeed: 0.7,
    minDistance: -1,
    maxDistance: 1,
  };
  const sceneSwitchDuration = 500;

  // json
  useEffect(() => {
    fetch('./positions.json')
      .then((res) => res.json())
      .then((json) => {
        //1
        const largest = getLargest(json);
        const newJson = json.map((val, ind) => {
          const newPositions = {
            position: val.position.map((_val, _ind) => {
              return _val / largest;
            }),
          };
          return Object.assign(val, newPositions);
        });
        setPositionArr(newJson);
      });
  }, []);

  //ChangeButton => onClick
  const handleClick = useCallback((e) => {
    if (isPcControls && window.DeviceOrientationEvent.requestPermission !== undefined && typeof window.DeviceOrientationEvent.requestPermission === 'function') {
      tryDeviceEvent()
        .then(function (res) {
          switchControls((isPcControls) => !isPcControls);
        })
        .catch(function (error) {
          alert('');
        });
    } else {
      switchControls((isPcControls) => !isPcControls);
    }
  }, []);

  //
  const endPreload = useCallback((e) => {
    const element = document.querySelector('#wrapper-preloader');
    element.setAttribute('data-is-active', false);
  }, []);

  if (positionArr.length == 0) {
    return <Fragment />;
  }

  const textures = [];
  positionArr.forEach((val, index) => {
    textures.push(`./textures/${index}.jpg`);
  });
  //
  const defaultScene =
    positionArr
      .filter((currentValue, index, array) => {
        return currentValue.default === true;
      })
      .shift() || positionArr[0];
  //
  const tentScene = positionArr
    .filter((currentValue, index, array) => {
      return currentValue.tent === true;
    })
    .shift();

  return (
    <Preload images={textures} autoResolveDelay={15000} onSuccess={endPreload} onError={() => console.log('Error:There was an error loading some of the images')}>
      <Fragment>
        {isMobile && <ChangeButton onClick={handleClick} />}
        <Canvas camera={{ position: [0, 0, -0.001], fov: 75 }} raycaster={{ filter: (items) => items.slice(0, 1) }}>
          <ambientLight />
          {isPcControls ? <PcControls ref={pcControlsRef} onChange={(angle) => miniMapRef.current.changeAngle(angle)} {...controlProps} /> : <MobileControls onChange={(angle) => miniMapRef.current.changeAngle(angle)} />}
          <AllScenes
            ref={allSceneRef}
            isPcControls={isPcControls}
            positionArr={positionArr}
            defaultScene={defaultScene}
            tentScene={tentScene}
            onChange={(tgtScene) => {
              miniMapRef.current.changeScene(tgtScene);
              buttonsRef.current.changeScene(tgtScene);
            }}
            duration={sceneSwitchDuration}
          />
          <axesHelper args={[300]} />
        </Canvas>
        <MiniMap ref={miniMapRef} positionArr={positionArr} onChange={(tgtScene) => allSceneRef.current.changeScene(tgtScene)} />
        <Buttons ref={buttonsRef} inTent={tentScene == defaultScene} onClickButton={(type) => allSceneRef.current.onClickButton(type)} tentScene={tentScene} />
      </Fragment>
    </Preload>
  );
}

export default App;
