import React, { useState, forwardRef, useImperativeHandle, useCallback } from 'react';

function Buttons({ onClickButton, tentScene, inTent }, ref) {
  const [isTent, changeIsTent] = useState(inTent);
  useImperativeHandle(ref, () => {
    return {
      changeScene: (scene) => {
        tentScene && scene == tentScene.name ? changeIsTent(true) : changeIsTent(false);
      },
    };
  });
  return (
    <div id="controlButton">
      <button data-tooltip="" onClick={() => onClickButton('zoomIn')}>
        +
      </button>
      <button data-tooltip="" onClick={() => onClickButton('zoomOut')}>
        -
      </button>
      {!isTent && (
        <button data-tooltip="" onClick={() => onClickButton('moveLeft')}>
          
        </button>
      )}
      {!isTent && (
        <button data-tooltip="" onClick={() => onClickButton('moveRight')}>
          
        </button>
      )}
      {tentScene && !isTent && (
        <button data-tooltip="" onClick={() => onClickButton('tentIn')}>
          
        </button>
      )}
      {tentScene && isTent && (
        <button data-tooltip="" onClick={() => onClickButton('tentOut')}>
          
        </button>
      )}
      <button data-tooltip="" onClick={() => onClickButton('reset')}>
        
      </button>
    </div>
  );
}
Buttons = forwardRef(Buttons);

function ChangeButton(props) {
  const { onClick } = props;
  return (
    <button id="changeButton" onClick={onClick}>
      Change
    </button>
  );
}

function MiniMap({ positionArr, onChange }, ref) {
  const [radian, changeRadian] = useState(Math.PI);
  const [directionPosition, changeScene] = useState(positionArr[0].position);
  const handleClick = useCallback((index) => {
    onChange(index);
  }, []);
  useImperativeHandle(ref, () => {
    //  ref.current 
    return {
      changeAngle: (angle) => changeRadian(-angle),
      changeScene: (scene) => {
        changeScene(positionArr[scene].position);
      },
    };
  });
  const degree = radian * (180 / Math.PI) + 180;
  const directionStyle = {
    transform: ' translate(' + -directionPosition[0] * 50 + 'px,' + -directionPosition[2] * 50 + 'px) rotate(' + (degree - 45) + 'deg)',
  };
  const points = [];
  positionArr.forEach((val, index) => {
    points.push(<div className="point" key={index} index={index} onClick={() => handleClick(index)} style={{ transform: 'translate(' + -val.position[0] * 50 + 'px,' + -val.position[2] * 50 + 'px)' }} />);
  });
  return (
    <div id="miniMap">
      <div id="direction" style={directionStyle} />
      {points}
      {/* {Object.keys(positionArr).map((key) => (
        <div className="point" key={key} index={key} onClick={handleClick} style={{ transform: 'translate(' + positionArr[key].position[0] / 5 + 'px,' + positionArr[key].position[2] / 5 + 'px)' }} />
      ))} */}
    </div>
  );
}

MiniMap = forwardRef(MiniMap);

export { ChangeButton, Buttons, MiniMap };
