import React, { useState, useRef, useMemo, useCallback, useEffect, forwardRef, useImperativeHandle } from 'react';
import { TextureLoader } from 'three/src/loaders/TextureLoader';
import * as THREE from 'three';
import { useThree } from 'react-three-fiber/web.cjs';

function AllScenes({ onChange, positionArr, duration, isPcControls, defaultScene, tentScene }, ref) {
  const { camera } = useThree();
  const [currentScene, changeScene] = useState(defaultScene.name);
  const [beforeScene, changeBeforeScene] = useState(false);
  const refs = useRef([]);
  const coverRef = useRef();
  useImperativeHandle(ref, () => {
    return {
      // 
      changeScene: (tgtScene) => handleChangeScene(tgtScene),
      onClickButton: (type) => handleClickButton(type),
    };
  });
  const handleChangeScene = useCallback(
    (tgtScene) => {
      changeBeforeScene(currentScene); //
      changeScene(tgtScene); //
    },
    [currentScene]
  );
  const handleClickButton = useCallback(
    (type) => {
      switch (type) {
        case 'zoomIn':
        case 'zoomOut':
          const currentFov = camera.fov;
          let addFov;
          if (type == 'zoomIn') {
            if (currentFov <= 45) return;
            addFov = -15;
          } else {
            if (currentFov >= 90) return;
            addFov = 15;
          }

          const smoothZoom = function (range) {
            let progress = 0;
            const easeOut = function (p) {
              return p * (2 - p);
            };
            const move = function () {
              progress++; // 
              const fov = range * easeOut(progress / 20);
              camera.fov = currentFov + fov;
              camera.updateProjectionMatrix();
              if (Math.abs(fov) < Math.abs(range)) {
                requestAnimationFrame(move);
              }
            };

            requestAnimationFrame(move); // 
          };

          smoothZoom(addFov);

          break;

        case 'tentIn':
          changeBeforeScene(currentScene); //
          changeScene(tentScene.name); //
          break;

        case 'tentOut':
          changeBeforeScene(currentScene); //
          changeScene(defaultScene.name); //
          break;

        case 'moveLeft':
          let leftScene = (currentScene + 1) % positionArr.length;
          if (tentScene && leftScene === tentScene.name) leftScene = (leftScene + 1) % positionArr.length;
          changeBeforeScene(currentScene); //
          changeScene(leftScene); //
          break;

        case 'moveRight':
          let rightScene = (positionArr.length + currentScene - 1) % positionArr.length;
          if (tentScene && rightScene === tentScene.name) rightScene = (positionArr.length + rightScene - 1) % positionArr.length;
          changeBeforeScene(currentScene); //
          changeScene(rightScene); //
          break;

        case 'reset':
          camera.fov = 75;
          camera.updateProjectionMatrix();
          if (isPcControls) {
            let newX, newY;
            if (positionArr[currentScene].angle === undefined) {
              //
              newX = positionArr[currentScene].position[0] / 1000;
              newY = positionArr[currentScene].position[2] === 0 ? -0.001 : positionArr[currentScene].position[2] / 1000;
            } else {
              newX = Math.cos((positionArr[currentScene].angle - 90) * (Math.PI / 180)) / 1000;
              newY = Math.sin((positionArr[currentScene].angle - 90) * (Math.PI / 180)) / 1000;
            }

            camera.position.set(newX / 10000, 0, newY / 10000);
          }
          /*
    const currentFov = camera.fov;
    const delay = 10;
    const duration = 100;
    const setFov = 75;
    const intervalFov = (setFov - currentFov) / (duration / delay);
    const start = Date.now();
    const timer = setInterval(function () {
      // 
      const timePassed = Date.now() - start;

      if (timePassed >= duration) {
        clearInterval(timer);
        camera.fov = setFov;
        camera.updateProjectionMatrix();
        return;
      }
      // timePassed 
      camera.fov += intervalFov;
      camera.updateProjectionMatrix();
    }, delay);
    */
          break;
      }
    },
    [currentScene]
  );

  useEffect(() => {
    //()
    onChange(currentScene); // & 
    if (beforeScene !== false) {
      refs.current[beforeScene].current.hide(currentScene);
    }
    coverRef.current.transition();
    refs.current[currentScene].current.show();
  }, [currentScene]);
  positionArr.forEach((val, index) => {
    refs.current[index] = React.createRef();
  });

  const scenes = [];
  positionArr.forEach((val, index) => {
    scenes.push(<Scene ref={refs.current[index]} key={index} index={index} positionArr={positionArr} duration={duration} isPcControls={isPcControls} position={val.position} arrows={val.arrows} angle={val.angle} onClick={(tgtScene) => handleChangeScene(tgtScene)} />);
  });

  return (
    <>
      {scenes}
      <TransitionCover ref={coverRef} duration={duration} />
    </>
  );
}

AllScenes = forwardRef(AllScenes);

function TransitionCover({ duration }, ref) {
  const meshRef = useRef();
  const materialRef = useRef();
  useImperativeHandle(ref, () => {
    return {
      transition: () => {
        const delay = 10;
        const start = Date.now(); // 
        const increment = 2 / (duration / delay);
        meshRef.current.scale.set(1, 1, 1);
        const timer = setInterval(function () {
          // 
          const timePassed = Date.now() - start;

          if (timePassed >= duration) {
            clearInterval(timer); // duration
            materialRef.current.opacity = 0;
            meshRef.current.scale.set(0, 0, 0);
            return;
          } else if (timePassed >= duration / 2) {
            materialRef.current.opacity -= increment;
            return;
          }

          // timePassed 
          materialRef.current.opacity += increment;
        }, delay);
      },
    };
  });
  return (
    <mesh ref={meshRef}>
      <sphereGeometry attach="geometry" args={[0.3, 10, 10]} />
      <meshBasicMaterial ref={materialRef} attach="material" side={THREE.BackSide} color="white" transparent={true} opacity={0} />
    </mesh>
  );
}
TransitionCover = forwardRef(TransitionCover);

function Scene({ index, onClick, positionArr, position, angle, arrows, duration, isPcControls }, ref) {
  const textureUrl = `./textures/${index}.jpg`;
  const group = useRef();
  const { camera } = useThree();
  const posA = [0, 0.001];
  const getAngle = useCallback((tgtPos, currentPos) => {
    const posB = [tgtPos[0] - currentPos[0], tgtPos[2] - currentPos[2]];
    const angle = Math.atan2(posB[1] - posA[1], posB[0] - posA[0]);
    return angle;
  }, []);
  const newArrows = arrows.map((val, ind) => {
    const angle = getAngle(positionArr[val].position, position);
    return <Arrow key={ind} tgtScene={val} angle={angle} onClick={onClick} />;
  });
  useImperativeHandle(ref, () => {
    //  ref.current 
    return {
      show: () => {
        setTimeout(() => {
          if (isPcControls) {
            let newX, newY;
            if (angle === undefined) {
              //
              newX = position[0] / 1000;
              newY = position[2] === 0 ? -0.001 : position[2] / 1000;
            } else {
              newX = Math.cos((angle - 90) * (Math.PI / 180)) / 1000;
              newY = Math.sin((angle - 90) * (Math.PI / 180)) / 1000;
            }
            camera.position.set(newX / 10000, 0, newY / 10000);
          }
          group.current.scale.set(1, 1, 1);
        }, duration / 2);
      },
      hide: (newScene) => {
        const angle = getAngle(positionArr[newScene].position, position);
        const directionX = -Math.cos(angle);
        const directionZ = -Math.sin(angle);
        const delay = 10;
        const _duration = duration / 5;
        const start = Date.now(); // 
        const timer = setInterval(function () {
          // 
          const timePassed = Date.now() - start;

          if (timePassed >= _duration) {
            clearInterval(timer); // duration
            group.current.scale.set(0, 0, 0);
            group.current.position.set(0, 0, 0);
            return;
          }

          // timePassed 
          group.current.position.set(group.current.position.x + directionX / 50, group.current.position.y, group.current.position.z + directionZ / 50);
        }, delay);
      },
    };
  });
  return (
    <group ref={group} scale={[0, 0, 0]}>
      {newArrows}
      <SkyBox textureUrl={textureUrl} />
    </group>
  );
}
Scene = forwardRef(Scene);

function SkyBox({ textureUrl }) {
  const texture = useMemo(() => new TextureLoader().load(textureUrl), [textureUrl]);
  /* rotation*/
  return (
    <mesh scale={[-1, 1, 1]} rotation={[0, Math.PI / 2, 0]}>
      <sphereGeometry attach="geometry" args={[0.7, 50, 50]} />
      <meshBasicMaterial attach="material" side={THREE.BackSide}>
        <primitive attach="map" object={texture} />
      </meshBasicMaterial>
    </mesh>
  );
}

function Arrow({ onClick, tgtScene, angle }) {
  const [distance, changeDistance] = useState(0.5);
  const position = [distance * Math.cos(angle), -0.2, distance * Math.sin(angle)];
  const textureUrl = './textures/arrow.png';
  const texture = useMemo(() => new TextureLoader().load(textureUrl), [textureUrl]);
  return (
    <mesh onClick={(e) => onClick(tgtScene)} onPointerDown={(e) => onClick(tgtScene)} onPointerOver={(e) => changeDistance(0.505)} onPointerOut={(e) => changeDistance(0.5)} position={position} rotation={[Math.PI / 2, 0, angle + Math.PI * 1.5]}>
      <circleGeometry attach="geometry" args={[0.03, 100]} />
      <meshPhongMaterial attach="material" side={THREE.DoubleSide}>
        <primitive attach="map" object={texture} />
      </meshPhongMaterial>
    </mesh>
  );
}

export { AllScenes };
