import React, { useRef, useEffect, useCallback, forwardRef, useImperativeHandle } from 'react';
import { useFrame, useThree, extend } from 'react-three-fiber/web.cjs';
import { DeviceOrientationControls } from 'three/examples/jsm/controls/DeviceOrientationControls';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

extend({ DeviceOrientationControls });
extend({ OrbitControls });

function PcControls(props, ref) {
  const { camera, gl } = useThree();
  const controls = useRef();
  const handleChange = useCallback((e) => {
    props.onChange(e.target.getAzimuthalAngle());
  }, []);
  useImperativeHandle(ref, () => {
    //  ref.current 
    return {
      resetAngle: resetAngle,
    };
  });
  const resetAngle = useCallback(() => {}, []);

  useFrame(() => controls.current.update());
  useEffect(() => {
    controls.current.addEventListener('change', handleChange);
  }, []);

  return <orbitControls ref={controls} args={[camera, gl.domElement]} target={[0, 0, 0]} change={handleChange} {...props} />;
}

PcControls = forwardRef(PcControls);

function MobileControls({ onChange }) {
  const { camera } = useThree();
  const controls = useRef();
  useFrame(() => {
    controls.current.update();
    onChange(camera.rotation.y);
  });
  return <deviceOrientationControls ref={controls} args={[camera]} />;
}

function tryDeviceEvent() {
  return new Promise(function (resolve, reject) {
    window.DeviceOrientationEvent.requestPermission()
      .then(function (response) {
        if (response == 'granted') {
          resolve();
        } else {
          throw new Error('no Permission');
        }
      })
      .catch(function (error) {
        reject('Falied!');
      });
  });
}

export { PcControls, MobileControls, tryDeviceEvent };
